document.addEventListener('DOMContentLoaded', function() {
    iniciarApp();
})

function iniciarApp() {
    crearGaleria();
    fijarNav();
    smoothScroll();
}

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes');

    for (let i=1; i<=12; i++) {
        const imagen = document.createElement('picture');

        imagen.innerHTML = ` <source srcset="build/img/thumb/${i}.avif" type="image/avif">
        <source srcset="build/img/thumb/${i}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt="Imagen galeria"> `;

        imagen.onclick = function() {
            mostrarImagen(i);
        }

        galeria.appendChild(imagen);
    }

    function mostrarImagen(id) {
        const imagen = document.createElement('picture');
    
        imagen.innerHTML = ` <source srcset="build/img/grande/${id}.avif" type="image/avif">
        <source srcset="build/img/grande/${id}.webp" type="image/webp">
        <img loading="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt="Imagen galeria"> `;
    
    
        //crea el overlay con la imagen
        const overlay = document.createElement('DIV');
        overlay.appendChild(imagen);
        overlay.classList.add('overlay');
        overlay.addEventListener('click', function() {
            const body = document.querySelector('body');
            body.classList.remove('fijar_body');
            overlay.remove();
        })
    
        //boton para cerrar el modal (foto)
        const cerrarVentana = document.createElement('P');
        cerrarVentana.textContent = 'x';
        cerrarVentana.classList.add('boton-close');
    
        cerrarVentana.addEventListener('click', function() {
            const body = document.querySelector('body');
            body.classList.remove('fijar_body');
    
            overlay.remove();
        });
    
        overlay.appendChild(cerrarVentana);
    
        //a√±ade el overlay al html
        const body = document.querySelector('body');
        body.appendChild(overlay);
            body.classList.add('fijar_body');  //esta clase solo aparece cuando se muestra el overlay o la imagen grande.
      
    }
}

function fijarNav () {
    const barra = document.querySelector('.header');
    const sobreFestival = document.querySelector('.sobre--festival')
    const body = document.querySelector('body');
    

    window.addEventListener('scroll', function() {
        const alturaHeader = barra.offsetHeight;

        if (sobreFestival.getBoundingClientRect().bottom - alturaHeader < 0) {
            barra.classList.add('fijar_navegacion');

                if (window.innerWidth>768) {
                    body.style.paddingTop = alturaHeader + "px"; //sirve para que cuando aparezca el nav el body no se corra hacia abajo
                }

        } else {
            barra.classList.remove('fijar_navegacion');
            body.removeAttribute('style');
        }
    });
}


function smoothScroll () {
    const enlaces = document.querySelectorAll ('.navegacion-principal a');

    enlaces.forEach(enlace => {
        enlace.addEventListener('click', function(evento) {
            evento.preventDefault();

            const reference = evento.target.attributes.href.value; //value es el id del elemento html
            const seccion = document.querySelector(reference);
            seccion.scrollIntoView({behavior: 'smooth'});
            
        })
    });
}


